<template>
  <v-container fluid>
    <div id="particles-js" />

    <intro />
    <icon-bar />
    <additional-info />
  </v-container>
</template>

<script>
import 'particles.js';
import Intro from '@/components/home/Intro.vue';
import IconBar from '@/components/home/IconBar.vue';
import AdditionalInfo from '@/components/home/AdditionalInfo.vue';

let particles = require('../assets/data/particles.json');

export default {
  props: ['dark'],

  components: {
    'intro': Intro,
    'icon-bar': IconBar,
    'additional-info': AdditionalInfo,
  },

  methods: {
    initParticles() {
      window.particlesJS("particles-js", particles);
    },
    updateParticleColour(colorHex, colorRgb) {
      pJSDom[0].pJS.particles.array.forEach((elem) => {
        elem.color.value = colorHex;
        elem.color.rgb = colorRgb;
      });
      pJSDom[0].pJS.particles.line_linked.color = colorHex;
      pJSDom[0].pJS.particles.line_linked.color_rgb_line = colorRgb;
    }
  },

  mounted() {
    this.initParticles();
  },

  watch: {
    dark: function(curr, prev) {
      let hex = this.dark ? '#ffffff' : '#000000';
      let rgb = this.dark ? { r: 255, g: 255, b: 255 } : { r: 0, g: 0, b: 0 };
      this.updateParticleColour(hex, rgb);
    },
  },
};
</script>

<style>
#particles-js {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
</style>
